import React, { useState, useEffect } from 'react'
import { MenuItems } from "./MenuItem"
// import MenuIcon from '@mui/icons-material/Menu';
// import CloseIcon from '@mui/icons-material/Close';
import styled from "styled-components";

function NavBar() {
    const [closed, change] = useState(true)
    const [navBar,setNavbar]= useState(false)
    const [width,setWidth] = useState(true)
    const [toggle, toggleNav] = useState(false);

    const changeBackground = ()=>{
        if (window.scrollY >= window.innerHeight - 60) {
            setNavbar(true)
          } else {
            setNavbar(false)
          }
    }
    const monitorWidth = ()=>{
        if (window.innerWidth > 950){
            setWidth(false)
            change(true)
        }else{
            setWidth(true)

        }
    }

    useEffect(() => {
        monitorWidth()
        // adding the event when scroll change background
        window.addEventListener("resize", monitorWidth)
      })
    
    useEffect(() => {
        changeBackground()
        // adding the event when scroll change background
        window.addEventListener("scroll", changeBackground)
      })
    const NavIcon = styled.button`
        background: none;
        cursor: pointer;
        border: none;
        outline: none;`;
    const Line = styled.span`
    display: block;
    border-radius: 50px;
    width: 25px;
    height: 3px;
    margin: 5px;
    background-color: #fff;
    transition: width 0.4s ease-in-out !important;
  
    :nth-child(2) {
      width: ${props => (props.open ? "40%" : "70%")};
    }
  `;
  const Item = styled.li``;

const Link = styled.a`
  color: white;
  text-decoration: none;

  :hover {
    text-decoration: underline;
  }
`;
  
  const Overlay = styled.div`
    position: relative;
    z-index: 1;
    height: ${props => (props.open ? "100vh" : 0)};
    width: 100vw;
    background: #1c2022;
    transition: height 0.4s ease-in-out !important;
    -webkit-transition: height 0.4s ease-in-out;
    
    @media (min-width: 950px) {
      display: none;
    }
  `;
  
  const OverlayMenu = styled.ul`
    list-style: none;
    position: absolute;
    left: 50%;
    top: 45%;
    transform: translate(-50%, -50%);

   
  
    li {
      opacity: ${props => (props.open ? 1 : 0)};
      font-size: 25px;
      margin: 50px 0px;

      transition: opacity 0.4s ease-in-out !important;
    }
  
    li:nth-child(2) {
      margin: 50px 0px;
    }
  `;
        

    return (
        <div className="navDiv">
        <nav className={navBar?"NavbarItems-down":"NavbarItems"}  >
            <div className={navBar?"NavbarLogo-down":"NavbarLogo" } >
                <h1 >
                    Rl
                </h1>
            </div>
            <div className="menu-icon">
            {/* <NavIcon>
                
            </NavIcon> */}
        <NavIcon onClick={() => toggleNav(!toggle)}>
          <Line className={navBar?"setBlack":"setWhite"} open={toggle} />
          <Line className={navBar?"setBlack":"setWhite"} open={toggle} />
          <Line className={navBar?"setBlack":"setWhite"} open={toggle} />
        </NavIcon>

                {/* {closed ? <button onClick={() => { //this function deals with closing and opening of menu icon
                    change(false)
                }}><MenuIcon /></button> : <button onClick={() => {
                    change(true)
                }}><CloseIcon /></button>} */}
            </div>
            
            

            <div className={closed ?"navbar-right" :width?"navbar-rightActive":"navbar-right" }>
                {MenuItems.map((item, index) => {
                    return <a key={index}  className={!navBar?"nav-links" : closed? "nav-links-down" :"nav-links-active-down"} href={item.url} onClick={()=>{
                        change(true)
                    }}>{item.title}</a>
                })}
            </div>
        </nav>
        <Overlay  open={toggle}>
        <OverlayMenu  open={toggle}>
                {MenuItems.map((item, index) => {
                    return <Item key ={index}>
                <Link key ={index} href={item.url} onClick={()=>{
                        toggleNav(!toggle)
                    }}>
                 {item.title}
                </Link>
                </Item>
                })}
           
   
        
        </OverlayMenu>
      </Overlay>
        </div>
    )
}


export default NavBar
